import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/Preloader";

const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader/>;
    }
    return (
        <div>
            <div className={s.image}>
                <img
                    src={'https://photos.smugmug.com/Wallpapers/i-7Tds9J5/0/9d189a52/X2/HDRshooter-super-ultra-wide-wallpaper-034-X2.jpg'}
                    alt={""} className={s.profileImg}/>
            </div>
            <div className={s.descriptionBlock}>
                <div>
                    <img
                        src={props.profile.photos.large ? props.profile.photos.large : "https://winaero.com/blog/wp-content/uploads/2018/08/Windows-10-user-icon-big.png"}
                        alt={"avatar"}/>
                </div>
                <div>ID: {props.profile.userId}</div>
                <div>Full Name: {props.profile.fullName ? props.profile.fullName : "Not indicated"}</div>
                <div>About me: {props.profile.aboutMe ? props.profile.aboutMe : "No information"}</div>
                <div>Looking for a job: {props.profile.lookingForAJob ? "Yes" : "No"}</div>
            </div>
        </div>
    );
}

export default ProfileInfo;
