import s from "./Users.module.css";
import userPhoto from "../../assets/images/user_avatar.png";
import React from "react";
import {NavLink} from "react-router-dom";
import axios from "axios";

const Users = (props) => {
    // let pagesCount = Math.ceil(props.totalUsersCount / props.pageSize);
    let pages = [];
    for (let i = 1; i <= 30; i++) {   /*pagesCount*/
        pages.push(i);
    }

    return (
        <div>
            <div className={s.navigation}>
                {pages.map(p => {
                    return <span className={props.currentPage === p && s.selectedPage}
                                 onClick={(e) => {
                                     props.onPageChanged(p);
                                 }}> {p} </span>
                })}
            </div>
            {props.users.map(u => <div key={u.id}>
                <span>
                    <div>
                        <NavLink to={'/first-react-app/profile/' + u.id}>
                            <img className={s.avatar} alt={"avatar"}
                                 src={u.photos.small != null ? u.photos.small : userPhoto}/>
                        </NavLink>
                    </div>
                    <div>
                        {u.followed
                            ? <button onClick={() => {
                                axios.delete(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`, {
                                    withCredentials: true,
                                    headers: {
                                        "API-KEY": "0f3107dc-d7f5-455d-ae6e-0283b9882006"
                                    }
                                })
                                    .then(response => {
                                        if (response.data.resultCode === 0){
                                            props.unfollow(u.id);
                                        }
                                    });
                            }}>Unfollow</button>
                            : <button onClick={() => {
                                axios.post(`https://social-network.samuraijs.com/api/1.0/follow/${u.id}`, {}, {
                                    withCredentials: true,
                                    headers: {
                                        "API-KEY": "0f3107dc-d7f5-455d-ae6e-0283b9882006"
                                    }
                                })
                                    .then(response => {
                                        if (response.data.resultCode === 0){
                                            props.follow(u.id);
                                        }
                                    });
                            }}>Follow</button>}
                    </div>
                </span>
                <span>
                    <span>
                        <div>{u.name}</div>
                        <div>{u.status ? `Status: ${u.status}` : null}</div>
                    </span>
                    <span>
                        <div>{"u.location.country"}</div>
                        <div>{"u.location.city"}</div>
                    </span>
                </span>
            </div>)
            }
        </div>
    );
}

export default Users;
