import s from "./Navbar.module.css";
import {NavLink} from "react-router-dom";
import SideFriends from "./SideFriends/SideFriends";
import React from "react";

const Navbar = (props) => {
        return (
            <nav className={s.nav}>
                <div className={s.item}>
                    <NavLink to="/first-react-app/profile" activeClassName={s.active}>
                        Profile
                    </NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to="/first-react-app/dialogs" activeClassName={s.active}>
                        Messages
                    </NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to="/first-react-app/feed" activeClassName={s.active}>
                        Feed
                    </NavLink>
                </div>
                <div className={s.item}>
                    <NavLink to="/first-react-app/users" activeClassName={s.active}>
                        Users
                    </NavLink>
                </div>
                <div className={s.item}>
                    <a href="/first-react-app/music">Music</a>
                </div>
                <div className={s.item}>
                    <a href="/first-react-app/settings">Settings</a>
                </div>
                <SideFriends sideFriends={props.sideFriends}/>
            </nav>
        );
    }
;

export default Navbar;
