const SEND_MESSAGE = "SEND_MESSAGE";
const UPDATE_NEW_MESSAGE_TEXT = "UPDATE_NEW_MESSAGE_TEXT";

let initialState = {
    messages: [
        {id: 1, message: "Hi", sender: "you"},
        {id: 2, message: "Hello", sender: "friend"},
        {id: 3, message: "How are you?", sender: "you"},
        {id: 4, message: "Good,and you?", sender: "friend"},
        {id: 5, message: "Good", sender: "you"},
        {id: 6, message: "Ti horoshiy", sender: "you"},
        {id: 7, message: "Sam ti horoshiy", sender: "friend"}
    ],
    dialogs: [
        {id: 1, name: "NeVlad"},
        {id: 2, name: "Vlad"},
        {id: 3, name: "VLADD"},
        {id: 4, name: "VLADD"},
        {id: 5, name: "VLADD"},
    ],
    newMessageText: ''
};


const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case SEND_MESSAGE:
            let body = state.newMessageText;
            return {
                ...state,
                newMessageText: '',
                messages: [...state.messages, {id: 8, message: body, sender: "you"}]
            }
        case UPDATE_NEW_MESSAGE_TEXT:
            return {
                ...state,
                newMessageText: action.newText
            };
        default:
            return state;
    }
}

export const sendMessageCreator = () => ({type: SEND_MESSAGE});
export const updateNewMessageTextCreator = (text) => {
    return {
        type: UPDATE_NEW_MESSAGE_TEXT,
        newText: text
    }
};

export default dialogsReducer;
