import SideFriend from "./SideFriend/SideFriend";
import s from './SideFriends.module.css';
const SideFriends = (props) => {
    let friends = props.sideFriends.map(f => (<SideFriend key={f.id} name={f.name}/>))
    return(
        <div className={s.sideFriendsBlock}>
            Friends
            <div className={s.friends}>
                {friends}
            </div>
        </div>
    );
}

export default SideFriends;
